import { useFinanceAllBank } from "../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import CustomSelectBank from "../../../Ui/Select/CustomSelectBank";
import { useTranslation } from "react-i18next";

const FeeStatisticsBankFilter = ({
  fid,
  onSelect,
  errorShow,
  viewAs,
  defalutValue,
  customStyleContainer,
  customListStyle,
}) => {
  const { t } = useTranslation();
  const { financeAllBank } = useFinanceAllBank({
    data: {
      fid: fid,
      page: 1,
      limit: 1000,
      search: "",
    },
    skip: !fid,
  });
  // console.info("sdjhfbsdj", financeAllBank?.list);

  return (
    <CustomSelectBank
      selectLabel={t("bank_account")}
      selectedValue={t("bank_account_placeholder")}
      options={
        financeAllBank?.list?.length > 0 ? [...financeAllBank?.list] : []
      }
      onClick={onSelect}
      isFull
      errorShow={errorShow}
      defalutValue={defalutValue}
      viewAs={viewAs}
      customStyleContainer={customStyleContainer}
      customListStyle={customListStyle}
    />
  );
};

export default FeeStatisticsBankFilter;
