import { useEffect, useState } from "react";
import Loading from "../../../Loader/Loading";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import FinanceDaybookHistoryCard from "./FinanceDaybookHistoryCard";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import style from "../FinanceManager.module.css";
import { useFinanceAllDaybookFilterGenerated } from "../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
const FinanceDaybookHistory = ({ fid }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const {
    financeAllDaybookFilterGenerated,
    financeAllDaybookFilterGeneratedLoading,
  } = useFinanceAllDaybookFilterGenerated({
    data: {
      fid: fid,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !fid,
  });

  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (financeAllDaybookFilterGenerated?.list?.length) {
      let obj = nextPage(financeAllDaybookFilterGenerated?.list?.length);
      setState(() => obj);
    }
  }, [financeAllDaybookFilterGenerated?.list]);

  const onSearchEvent = (e) => {
    if (
      !financeAllDaybookFilterGeneratedLoading &&
      financeAllDaybookFilterGenerated?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };
  return (
    <>
      <div className={style.fm_search_full}>
        <section className={style.fm_search_full_container}>
          <div className={style.fm_search_full_container_inner}>
            <img
              className={style.fm_search_full_icon}
              alt="search icon"
              src={`${assestsNavbarUrl}/navbar-search.svg`}
            />
            <input
              type="text"
              placeholder={t("search")}
              onChange={onSearchEvent}
              onKeyUp={onCallBindSearch}
              value={searchCopy}
            />
          </div>
        </section>
      </div>

      {financeAllDaybookFilterGenerated?.list?.map((excel, index) =>
        financeAllDaybookFilterGenerated?.list?.length === index + 1 ? (
          <div key={excel?._id} ref={ref}>
            <FinanceDaybookHistoryCard excel={excel} />
          </div>
        ) : (
          <FinanceDaybookHistoryCard excel={excel} key={excel?._id} />
        )
      )}
      {financeAllDaybookFilterGeneratedLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <Loading />
        )
      ) : search ? (
        financeAllDaybookFilterGenerated?.list?.length === 0 && (
          <EmptyMessage
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
            title="No daybook found related this search."
          />
        )
      ) : (
        financeAllDaybookFilterGenerated?.list?.length === 0 && (
          <EmptyMessage
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
            title="No daybook added."
          />
        )
      )}
    </>
  );
};

export default FinanceDaybookHistory;
